<template>
  <div class="addMouduleSidebar">
    <!-- <HelloWorld msg="Welcome to Your Vue.js App" /> -->
    <div class="addTabModuleButton">
      <el-button style="border-radius: 20px" @click="dialogFormVisible = true"
        >添加新侧边栏模块</el-button
      >
      <!-- 点击增加新模块，打开对话框，在对话框中完成新模块的添加 -->
    </div>

    <!-- 导航栏模块 -->
    <div class="addMouduleTabTableOut">
      <el-table :data="tableTabData" border style="width: 100%">
        <!-- 第一栏el-table-column是第一列，是哪个点击之后能展开的小三角 -->
        <el-table-column type="expand">
          <!-- 通过设置 type="expand" 和 Scoped slot 可以开启展开行功能，el-table-column 的模板会被渲染成为展开行的内容，展开行可访问的属性与使用自定义列模板时的 Scoped slot 相同。 -->
          <template slot-scope="props">
            <!-- 框架：表格 -->
            <!-- props就是tableTabData整个数组， props.row是对象里面的每一个小对象 -->
            <div class="addMouduleTabTableOut">
              <el-table
                class="addMouduleTabTable"
                :data="props.row.tableSidebarData"
                border
              >
                <el-table-column
                  prop="titleName"
                  label="侧边栏"
                ></el-table-column>
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <el-button
                      @click="editModuleSiderbar(scope.$index, scope.row)"
                      >编辑</el-button
                    >
                    <el-button
                      type="danger"
                      @click="deleteModuleSiderbar(scope.$index, scope.row)"
                      >删除</el-button
                    >
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          label="所属导航栏（展开导航栏即可查看导航栏模块中的侧边栏模块）"
          prop="tabName"
        ></el-table-column>
      </el-table>

      <!-- 对话框模块，用以添加新的侧边栏 -->
      <el-dialog title="收货地址" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="新侧边栏名称" :label-width="formLabelWidth">
            <el-input v-model="form.titleName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="所属导航栏" :label-width="formLabelWidth">
            <el-select v-model="form.tableName" placeholder="请选择所属导航栏">
              <!-- label是显示出来的，value是实际含义，传给后台的实际选择这个内容是什么含义 -->
              <el-option
                v-for="(item, index) in tableTabData"
                :key="index"
                :label="item.tabName"
                :value="item.tabName"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button
            type="primary"
            @click="addModuleSiderbar(form.tableName, form.titleName)"
            >确 定</el-button
          >
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
import axios from "axios";

export default {
  name: "AddMouduleSidebar",
  components: {
    // HelloWorld
  },
  data() {
    return {
      // tableTabData: [
      // {
      //   // 这个名字要跟后端一致
      //   tabName: "学院新闻",
      //   tableSidebarData: [
      //     {
      //       sidebarName: "学院新闻1",
      //     },
      //     {
      //       sidebarName: "学院新闻2",
      //     },
      //     {
      //       sidebarName: "学院新闻3",
      //     },
      //   ],
      // },
      //   {
      //     tabName: "通知公告",
      //     tableSidebarData: [
      //       {
      //         sidebarName: "通知公告1",
      //       },
      //       {
      //         sidebarName: "通知公告2",
      //       },
      //       {
      //         sidebarName: "通知公告3",
      //       },
      //     ],
      //   },
      // ],
      tableTabData: [],
      // 对话框弹出框，添加新的侧边栏模块
      dialogFormVisible: false,
      form: {
        titleName: "", // 侧边栏名称
        tableName: "", // 所属导航栏
      },
      formLabelWidth: "120px",
    };
  },
  mounted() {
    // 接收导航栏列表
    axios
      .post("/showTables")
      .then((response) => {
        console.log(response);
        console.log(response.object);
        // 接收列表
        this.tableTabData = response.object;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    // 增加侧边栏新模块
    addModuleSiderbar(tableName, titleName) {
      this.dialogFormVisible = false; // 关闭对话框

      // 获取所属导航栏
      console.log(tableName); // 是所属导航栏
      console.log(titleName); // 是侧边栏名称

      // 与后台传值
      axios
        .post("/CreateTitle", {
          tableName: tableName,
          titleName: titleName,
        })
        .then((response) => {
          console.log(response);
          console.log(response.object);
        })
        .catch((error) => {
          console.log(error);
        });

      // 为了刷新页面并且不让页面闪一下影响观感
      this.$router.replace({
        path: "/nullabc.vue",
        name: "Nullabc",
        params: {
          path: "/admin/addModuleSidebar",
          name: "AddModuleSidebar",
        },
      });
    },
    // 编辑侧边栏模块
    editModuleSiderbar(index, row) {
      console.log(index, row);

      // 注释：旧的导航栏名称 row.titleName;
      console.log(row.titleName);
      // 弹框提示输入导航栏新名称
      this.$prompt("请输入此侧边栏模块新名称名称", "修改侧边栏名称", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputPattern: /\S/,
        inputErrorMessage: "不准为空",
      }).then(({ value }) => {
        console.log(value);
        // 修改导航栏名称传值 -- axios传值
        axios
          .post("/changetitles", {
            titleName: row.titleName,
            titleNewName: value,
          })
          .then((response) => {
            console.log(response);
            console.log(response.object); // 数组

            // 数据成功回传之后，跳出来massage提示用户所上传的模块的名称
            this.$message({
              type: "success",
              message: "修改后侧边栏模块名称: " + value,
            });
            // 为了刷新页面并且不让页面闪一下影响观感
            this.$router.replace({
              path: "/nullabc.vue",
              name: "Nullabc",
              params: {
                path: "/admin/addModuleSidebar",
                name: "AddModuleSidebar",
              },
            });
          })
          .catch((error) => {
            console.log(error);

            // 数据成功回传之后，跳出来massage提示用户error错误
            this.$message({
              type: "info",
              message: error,
            });
          });
      });
    },
    // 删除侧边栏模块
    deleteModuleSiderbar(index, row) {
      console.log(index, row);
      console.log(row.titleName);
      axios
        .post("/deletetitle", {
          titleName: row.titleName,
        })
        .then((response) => {
          console.log(response);
          console.log(response.object); // 数组

          // 为了刷新页面并且不让页面闪一下影响观感
          this.$router.replace({
            path: "/nullabc.vue",
            name: "Nullabc",
            params: {
              path: "/admin/addModuleSidebar",
              name: "AddModuleSidebar",
            },
          });
        })
        .catch((error) => {
          console.log(error);

          // 数据成功回传之后，跳出来massage提示用户error错误
          this.$message({
            type: "info",
            message: "删除" + row.sidebarName + "侧边栏报错：" + error,
          });
        });
    },
  },
};
</script>


<style>
.addTabModuleButton {
  float: right;
  padding: 15px;
  margin: 0 auto;
}
.addMouduleTabTableOut {
  margin: 10px;
}
.addMouduleTabTable {
  width: 50%;
  border-radius: 10px;
}
</style>