    // HelloWorld
  },
  // props: ["loginResults", "entryAdmin"],
  methods: {
    SideLoginWindows() {
      // this.$emit("loginWindows");
      // 打开登陆弹窗
      this.$store.commit("isLoginWindows"); // 这里使用 $store.commit 提交 isLoginWindows（函数）
