<template>
  <div id="app">
    <!-- 登陆弹窗和灰色蒙版 -->
    <Grey v-if="this.$store.state.isLoginWindows"></Grey>
    <Login
      v-if="this.$store.state.isLoginWindows"
      @loginWindows="AppLoginWindows()"
      @loginResults="AppLogonResults()"
    ></Login>

    <!-- 侧边工具栏 -->
    <SideFunction @entryAdmin="AppEntryAdmin()" @loginWindows="AppLoginWindows()"></SideFunction>

    <!-- 页面主体 -->
    <div class="content">
      <!-- 路由到home页面，将home页面的东西展示出来 -->
      <router-view />
    </div>
  </div>
</template>

<script>
import Login from "@/components/Login.vue";
import Grey from "@/components/Grey.vue";
import SideFunction from "@/components/SideFunction.vue";

export default {
  name: "app",
  components: {
    // HelloWorld
    Login,
    Grey,
    SideFunction,
  },
  data() {
    return {
      // 登录弹窗显示与否
      // loginWindowsType: false,
      // 登陆状态成功与否
      // loginResults: true,
      // 是否进入后台管理页面
      // entryAdmin: false,
    };
  },
  methods: {
    // 点击登录，显示登录窗口
    AppLoginWindows() {
      // this.loginWindowsType = !this.loginWindowsType;
      // console.log(this.loginWindowsType);
      // 打开登陆弹窗
      this.$store.commit("isLoginWindows"); // 这里使用 $store.commit 提交 isLoginWindows（函数）
      console.log(this.$store.state.isLoginWindows);
    },
    // 登陆结果
    AppLogonResults() {
      // this.loginResults = !this.loginResults;
      // console.log(this.loginResults);
      // 是否登陆状态，从false取反编程true
      this.$store.commit("isLogin"); // 这里使用 $store.commit 提交 isLogin（函数）
      // 这样显式地提交(commit) mutations,可以让我们更好的跟踪每一个状态的变化，
      console.log(this.$store.state.isLogin);
    },
    // 进入后台管理界面
    AppEntryAdmin() {
      // this.entryAdmin = !this.entryAdmin;
      // console.log("this.entryAdmin" + this.entryAdmin);
      // 进入管理员登陆页面
      this.$store.commit("isAdminPage"); // 这里使用 $store.commit 提交 isLoginWindows（函数）
      console.log(this.$store.state.isAdminPage);
    },
  },
};
</script>

