<template>
  <div class="login">
    <!-- 顶部 -->
    <div class="xtop">
      <span class="x" @click="XClose()">X</span>
    </div>
    <!-- 表单 -->
    <div class="form">
      <el-form :label-position="labelPosition" label-width="80px" :model="form">
        <el-form-item label="登录名称">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="用户密码">
          <el-input v-model="form.password" show-password></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="loginButton" type="primary" @click="submitForm()"
            >登录</el-button
          >
          <!-- <el-button @click="resetForm('ruleForm')">重置</el-button> -->
        </el-form-item>
      </el-form>
    </div>
  </div>

  <!-- <HelloWorld msg="Welcome to Your Vue.js App" /> -->
  <!-- </div> -->
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
import axios from "axios";

export default {
  name: "Login",
  components: {
    // HelloWorld
  },
  data() {
    return {
      // 表单文字右对齐
      labelPosition: "right",
      form: {
        username: "",
        password: "",
      },
    };
  },
  methods: {
    XClose() {
      // this.$emit("loginWindows");

      // 为什么有下面这行注释在就无法执行上面那行代码
      // console.log(this.$emit("loginWindowsTypeChange"));

      // 关闭登陆弹窗
      // this.$emit("loginWindows");
      this.$store.commit("isLoginWindows"); // 这里使用 $store.commit 提交 isLoginWindows（函数）
      console.log(this.$store.state.isLoginWindows);
    },

    submitForm() {
      axios
        .post("/login", {
          username: this.form.username,
          password: this.form.password,
        })
        // function(response)
        .then((response) => {
          console.log(response);
          console.log(response.data);

          if (response.code == 200) {
            console.log("登陆成功");
            // 登录状态切换为成功
            // this.$emit("loginResults");
            this.$store.commit("isLogin"); // 这里使用 $store.commit 提交 isLogin（函数）
            // 这样显式地提交(commit) mutations,可以让我们更好的跟踪每一个状态的变化，
            console.log(this.$store.state.isLogin);

            // 关闭登陆弹窗
            // this.$emit("loginWindows");
            this.$store.commit("isLoginWindows"); // 这里使用 $store.commit 提交 isLoginWindows（函数）
            console.log(this.$store.state.isLoginWindows);

            // 弹出登陆成功
            this.$message({
              message: "登陆成功",
              center: true,
            });

            // 修改进入管理员页面
            this.$store.commit("isAdminPage"); // 这里使用 $store.commit 提交 isLoginWindows（函数）
            console.log(this.$store.state.isAdminPage);

            // 跳转页面到admin页面
            this.$router.push("/admin");
          } else if (response.code == 400) {
            console.log("登陆失败");
            // 关闭登陆弹窗
            // this.$emit("loginWindows");

            // 关闭登陆弹窗
            // this.$emit("loginWindows");
            this.$store.commit("isLoginWindows"); // 这里使用 $store.commit 提交 isLoginWindows（函数）
            console.log(this.$store.state.isLoginWindows);

            // 弹出登陆失败
            this.$message({
              message: "登陆失败",
              center: true,
            });
          }
        })
        // function(error)
        .catch((error) => {
          console.log(error);
          console.log("连接失败");
          // 关闭登陆弹窗
          // this.$emit("loginWindows");
          // 关闭登陆弹窗
          // this.$emit("loginWindows");
          this.$store.commit("isLoginWindows"); // 这里使用 $store.commit 提交 isLoginWindows（函数）
          console.log(this.$store.state.isLoginWindows);

          // 弹出连接失败
          this.$message({
            message: "连接失败",
            center: true,
          });
        });
    },
  },
};
</script>


<style>
.login {
  /* border: ghostwhite 2px solid; */
  background-color: RGB(241, 243, 244);
  position: absolute;

  width: 30%;
  top: 20%;
  left: 35%;
  z-index: 1500;

  /* display: none; */
}

.xtop {
  /* border: rgb(188, 174, 137) 1px solid; */

  background: rgb(223, 225, 226);
  width: 100%;
  height: 30px;
  margin-bottom: 30px;
  line-height: 30px; /*取值为百分比时候的描述：基于当前字体尺寸的百分比行间距 */
}
.x {
  float: right;
  padding: 0 4px;
  width: 5%;
  height: 30px;
  font-size: large;
  position: relative;
}
.x:hover {
  color: white;
  cursor: pointer;
  background: rgb(0, 153, 255);
}
.form {
  padding: 20px 30px;
}
/* .loginButton { */
/* margin: 0 auto; */
/* } */
</style>

