<template>
  <div class="oneToN">
    <!-- <HelloWorld msg="Welcome to Your Vue.js App" /> -->
    <div class="contantleft">
      <div class="center_left_box">
        <div class="box_titledown">
          <img src="../assets/details.jpg" />
          {{ this.$route.query.tableName }}&nbsp;&nbsp;&nbsp;
        </div>
        <ul>
          <li v-for="(item, index) in leftList" :key="index">
            <img src="../assets/bullet.jpg" />&nbsp;&nbsp;
            <a href="javascript:;" @click="changeRightListNow(item)">{{
              item.titleName
            }}</a>
          </li>
        </ul>
      </div>
    </div>

    <!-- 右半边 -->
    <div class="Details_contantright">
      <div class="oneToNDetails_title">
        &nbsp;&nbsp;
        <img alt src="../assets/redarrow.jpg" />
        &nbsp;&nbsp;{{ this.rightNowTitle }}
      </div>
      <div class="more_contantright">
        <div id="UpdatePanel2">
          <div class="dataListstype">
            <table
              id="DataList1"
              cellspacing="0"
              border="0"
              style="background-color: Transparent; border-collapse: collapse"
            >
              <tbody v-if="!this.$store.state.isLogin">
                <tr>
                  <td>
                    <ul>
                      <li
                        class="message"
                        v-for="(item, index) in rightListNow"
                        :key="index"
                      >
                        <img src="../assets/05.jpg" />
                        <a href="javascript:;" @click="toDetail(item)">{{
                          item.informName
                        }}</a>
                        <span style="float: right"
                          >[{{ item.informDate }}]</span
                        >
                      </li>
                      <span>
                        <hr
                          style="
                            height: 1px;
                            border: none;
                            border-top: 1px dashed #cccccc;
                          "
                        />
                      </span>
                    </ul>
                  </td>
                </tr>
              </tbody>

              <tbody v-if="this.$store.state.isLogin">
                <tr>
                  <td>
                    <ul>
                      <li
                        class="message"
                        v-for="(item, index) in rightListNow"
                        :key="index"
                      >
                        <img src="../assets/05.jpg" />
                        <a href="javascript:;" @click="toDetail(item)">{{
                          item.informName
                        }}</a>
                        <span style="float: right"
                          >[{{ item.informDate }}]</span
                        >

                        <span
                          style="float: right"
                          class="cursorPointer"
                          @click="sidebarListDelete(item)"
                          >删除</span
                        >
                        <span
                          style="float: right"
                          class="cursorPointer"
                          @click="sidebarListModify(item)"
                          >编辑</span
                        >
                      </li>
                      <span>
                        <hr
                          style="
                            height: 1px;
                            border: none;
                            border-top: 1px dashed #cccccc;
                          "
                        />
                      </span>
                    </ul>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <!-- 分页 -->
          <!-- <Pagination :moduleValue="fatherModuleValue" /> -->
          <!-- <el-pagination
            class="pagination"
            @current-change="handleCurrentChange"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="totalPage"
          ></el-pagination> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
import axios from "axios";
// import Pagination from "@/components/Pagination.vue";

export default {
  name: "OneToN",
  components: {
    // HelloWorld
    // Pagination,
  },
  data() {
    return {
      // // 这个结构用不了，但是可以当作参考
      // leftList: [
      //   {
      //     leftTitle: "leftList111",
      //     rightList: [
      //       {
      //         informName: "水水水水水RightItemTitle222",
      //         informDate: "RightItemDate222",
      //         inform: "RightItemContent222",
      //       },
      //       {
      //         informName: "RightItemTitle111",
      //         informDate: "RightItemDate111",
      //         inform: "RightItemContent111",
      //       },
      //     ],
      //   },
      // ],
      // leftList: [
      //   { leftListTitle: "aaasdfghj", leftId: 0 },
      //   { leftListTitle: "wertyh", leftId: 1 },
      //   { leftListTitle: "xxcvbn", leftId: 2 },
      // ],
      leftList: [],
      leftNowTitle: "",
      leftIdToRight: 0, // 也可以this.leftList.leftId
      rightListNow: [
        // {
        //   informName: "水水水水水RightItemTitle222",
        //   informDate: "RightItemDate222",
        //   inform: "RightItemContent222",
        // },
        // {
        //   informName: "RightItemTitle111",
        //   informDate: "RightItemDate111",
        //   inform: "RightItemContent111",
        // },
      ],
      rightNowTitle: "", //右边小标题
      // 传值到分页组件的模块值，分页组件中根据这个值判断后端
      // fatherModuleValue: "",
      // totalPage: 0, // 总页数
      // pageSize: 0, // 每页显示个数选择器的选项设置
      // leftId: 0,
      // deathPage: 1,
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      // this.$route.query.和this.$route.params.都是接收路由传值的
      console.log("zheli是oneToN页面");
      // tableName为了传给后台从而请求
      // 这个query就是传过来的对象
      console.log(this.$route.query.tableName);

      // 初始化左侧小标题
      this.leftNowTitle = this.$route.query.tableName;

      // 初始化左侧列表
      axios
        .post("/showTitles", {
          tableName: this.$route.query.tableName,
        })
        .then((response) => {
          console.log(response);
          console.log(response.object); // 数组
          // 本页面接收左侧列表
          this.leftList = response.object;
          // 接受左侧ID
          this.leftId = this.leftList[0].titlesId;
          console.log("初始化的时候，左侧第一个，titlesId：" + this.leftId);

          // 初始化右侧小标题--就是左侧所点击的
          this.rightNowTitle = this.leftList[0].titleName;

          // ---------------
          // 初始化右侧列表
          console.log("初始化右侧列表");
          console.log(this.$store.state);
          axios
            .post("/showInforms", {
              titleName: this.leftList[0].titleName,
            })
            .then((response) => {
              console.log(1);
              console.log(response);
              console.log(response.object);
              // 接收右侧列表
              this.rightListNow = response.object;

              console.log("next:从后端接收分页相关信息");
              console.log("检查传过后台之前，检查leftId：" + this.leftId);
              // -----
              // 从后端接收分页相关信息;
              // axios
              //   .get("/page", {
              //     titlesId: this.leftId, //"传过去一个值，让后台判断是哪个页面的分页"-----------侧边栏id
              //     InformsPagenum: this.deathPage, // 传过去页码，第几页
              //   })
              //   .then((response) => {
              //     console.log(response);
              //     this.pageSize = response.pageSize; // 每页显示个数选择器的选项设置
              //     this.totalPage = response.object.total; // 总页数

              //     console.log("每页显示个数选择器的选项设置:" + this.pageSize);
              //     console.log("传过去页码，第几页" + this.totalPage);
              //   })
              //   .catch((error) => {
              //     console.log(error);
              //   });
            })
            .catch((error) => {
              console.log(error);
            });
        })
        .catch((error) => {
          console.log(error);
        });
    },
    changeRightListNow(item) {
      console.log(item);
      // 接受左侧ID
      this.leftId = item.titlesId;
      console.log(this.leftId);
      // 点击左边改变右边列表
      axios
        .post("/showInforms", {
          titleName: item.titleName,
        })
        .then((response) => {
          console.log(response);
          console.log(response.object);
          // 接收右侧列表
          this.rightListNow = response.object;

          // 从后端接收分页相关信息;
          // axios
          //   .get("/page", {
          //     titlesId: this.leftId, //"传过去一个值，让后台判断是哪个页面的分页"-----------侧边栏id
          //     InformsPagenum: this.deathPage, // 传过去页码，第几页
          //   })
          //   .then((response) => {
          //     console.log(response);
          //     this.pageSize = response.pageSize; // 每页显示个数选择器的选项设置
          //     this.totalPage = response.object.total; // 总页数

          //     console.log(this.pageSize);
          //     console.log(this.totalPage);
          //   })
          //   .catch((error) => {
          //     console.log(error);
          //   });
        })
        .catch((error) => {
          console.log(error);
        });
      // 点击改变右侧小标题
      this.rightNowTitle = item.titleName;
    },
    toDetail(item) {
      // 跳转详情页
      console.log(item);
      this.$router.push({
        name: `DetailPage`, // 这里只能是name，对应路由
        params: { item: item },
      });
    },
    // 改变当前页
    handleCurrentChange(val) {
      // 这个val是个啥
      console.log(`当前页: ${val}`);
      console.log(val);

      // 改变当前页，传给后台----？？？？啥
      axios
        .get("/page", {
          titlesId: this.leftId, //"传过去一个值，让后台判断是哪个页面的分页"-----------侧边栏id
          InformsPagenum: val, // 传过去页码，第几页
        })
        .then((response) => {
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    //////////编辑和删除
    // 编辑
    sidebarListModify(item) {
      console.log(item); //item-(object):inform: "内容3"informDate: "日期3"informName: "tzgg标题3"informUrl: "模块3"

      // 将item传值到发布页面
      this.$store.commit("newItem", item); // 这里使用 $store.commit 提交 newItem,并将 item 传给 msg,从而修改 item
      // 这样显式地提交(commit) mutations,可以让我们更好的跟踪每一个状态的变化，
      console.log(this.$store.state.item); // 这个就是所获取的对象
      console.log("公告编辑");

      // 将编辑切换为ture
      this.$store.commit("isEdit", true); // 这里使用 $store.commit 提交 isEdit
      // 这样显式地提交(commit) mutations,可以让我们更好的跟踪每一个状态的变化，
      console.log(this.$store.state.isEdit);

      this.$router.replace({
        path: "/admin/publishPlus",
        name: "PublishPlus",
      });
    },

    // 删除
    sidebarListDelete(item) {
      console.log("index:" + item.index);
      console.log("item:" + item);
      console.log("item.informUrl:" + item.informUrl);
      console.log("item.informName:" + item.informName);
      // this.noticeList.splice(index, 1);
      // alert("删除");
      // 把id传给后台
      axios
        // 删除
        .post(
          "/Delete",
          // {
          // Id: index,
          // informUrl: this.noticeList[index].informUrl
          // }
          item
        )
        .then((response) => {
          console.log(response);
          console.log(response.object); // 数组
          alert("删除成功");

          // 为了刷新页面并且不让页面闪一下影响观感
          this.$router.replace({
            path: "/nullabc.vue",
            name: "Nullabc",
            params: {
              path: "/homePage.vue",
              name: "HomePage",
            },
          });
        })
        .catch((error) => {
          console.log(error);
        });
      console.log("侧边栏下面文章删除");
    },
  },
  watch: {
    // $route: function () {
    //   console.log("路有变化");
    // },

    $route(to, from) {
      console.log(from);
      console.log(to);
      this.init();
    },
  },
};
</script>


<style>
.oneToN {
  margin: 10px auto 0;
  padding: 0px;
  width: 970px;
  text-align: center;
  clear: both;
}
.contantleft {
  width: 257px;
  height: 163px;
  float: left;
}
.center_left_box {
  width: 250px;
  height: auto !important;
  min-height: 555px;
  float: left;
  background: url("../assets/center_box_bg.jpg") repeat-y;
}
.box_titledown {
  background-color: #0099ff;
  text-align: left;
  width: 250px;
  height: 20px;
  padding: 5px 0 5px 0;
  color: #ffffff;
  font-weight: bold;
}
.center_left_box ul {
  list-style: none;
  text-align: center;
  margin-left: 0;
}
.center_left_box ul li {
  margin: 10px auto 0;
  padding: 0;
  width: 150px;
  text-align: left;
  border-bottom: #ff3300 1px dashed;
  height: 30px;
  font-size: 14px;
}
/* 右半边 */
.Details_contantright {
  width: 700px;
  margin: 0px;
  float: right;
  padding: 0;
}
.oneToNDetails_title {
  background-color: #0099ff;
  text-align: left;
  font-size: 12px;
  width: 700px;
  height: 20px;
  padding: 5px 0 5px 0;
  color: #ffffff;
  font-weight: bold;
}
.more_contantright {
  position: relative;
  width: 700px;
  height: auto !important;
  height: 500px;
  min-height: 500px;
  margin: 0px;
  float: right;
  padding: 0;
  background-color: #f3f5f6;
}
#DataList1 {
  border: 0px;
  width: 680px;
  float: left;
}
#DataList1 li {
  float: left;
  text-align: left;
  vertical-align: bottom;
  margin: 10px 0 0 0;
  padding: 0px;
  width: 680px;
  height: auto;
  border-bottom: #ffcc99 1px dashed;
}
#DataList1 ul li img {
  margin-right: 4px;
  border: none;
}
#DataList1 ul li a,
#DataList1 ul li a:link,
#DataList1 ul li a:visited {
  color: #000000;
  text-decoration: none;
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
}
.message span {
  vertical-align: baseline;
  direction: ltr;
  text-indent: 2em;
  line-height: 25px;
  text-align: left;
  font-size: 12px;
}
.pagination {
  width: 500px;
  position: absolute;
  bottom: 0px;
}
</style>