import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../components/Login.vue'
import Grey from '../components/Grey.vue'
import SideFunction from '../components/SideFunction.vue'
import HomePage from '../components/HomePage.vue'
import Admin from '../components/Admin.vue'
import Editor from '../components/Editor.vue'
import PublishPlus from '../components/PublishPlus.vue'
import DetailPage from '../components/DetailPage.vue'

import Nullabc from '../components/0.vue'
import FileDownload from '../components/FileDownload.vue'
import FileUpload from '../components/FileUpload.vue'
// import OneToOne from '../components/OneToOne.vue'
import OneToN from '../components/OneToN.vue'

// 分页
// import Pagination from '../components/Pagination.vue'

// 增删模块
import AddModule from '../components/AddModule.vue'
import AddModuleTab from '../components/AddModuleTab.vue'
import AddModuleSidebar from '../components/AddModuleSidebar.vue'


Vue.use(VueRouter)



// 定义一个数组，数组里面每一项都是一个配置
const routes = [
  {
    // 充当主页面
    path: '/',
    name: 'Home',
    component: Home,  // 可以有很多组件，到时候根据name属性里面的值确定是哪一个页面
    redirect: '/homePage',// 进入页面之后默认显示homePage页面
    children: [
      {
        // tab选项卡下面那一段主要部分
        path: '/homePage',
        name: 'HomePage',
        component: HomePage,
      },
      {
        // 详情页
        path: '/detailPage',
        name: 'DetailPage',
        component: DetailPage
      },
      // {
      //   // 组件1（学院概况和学生工作）
      //   path: '/oneToOne',
      //   name: 'OneToOne',
      //   component: OneToOne
      // },
      {
        // 组件（除了学院概况和学生工作，其他所有）
        path: '/oneToN',
        name: 'OneToN',
        component: OneToN,
        // children: [
        //   {
        //     // 分页
        //     path: '/pagination',
        //     name: 'Pagination',
        //     component: Pagination
        //   },
        // ]
      },
      {
        // 文件下载
        path: '/fileDownload',
        name: 'FileDownload',
        component: FileDownload
      },

    ]
  },

  {
    path: '/login',
    name: 'Login',
    component: Login,
    children: [
      {
        path: '/sideFunction',
        name: 'SideFunction',
        component: SideFunction
      },
    ]
  },
  {
    path: '/grey',
    name: 'Grey',
    component: Grey
  },
  {
    path: '/admin',
    name: 'Admin',
    component: Admin,
    redirect: '/admin/addModule',// 进入管理页面之后默认显示发布
    children: [

      {
        path: '/admin/addModule',
        name: 'AddModule',
        component: AddModule,
        redirect: '/admin/addModule/addModuleTab',// 进入页面之后默认显示addModuleTab页面

        children: [
          {
            // 增加导航栏模块
            path: '/admin/addModule/addModuleTab',
            name: 'AddModuleTab',
            component: AddModuleTab
          },
          {
            // 增加侧边栏模块
            path: '/admin/addModule/addModuleSidebar',
            name: 'AddModuleSidebar',
            component: AddModuleSidebar
          },
        ]
      },
      {
        path: '/admin/publishPlus',
        name: 'PublishPlus',
        component: PublishPlus
      },
      {
        // 文件上传
        path: '/admin/fileUpload',
        name: 'FileUpload',
        component: FileUpload
      },

    ]
  },


  {
    path: '/editor',
    name: 'Editor',
    component: Editor
  },
  {
    path: '/nullabc',
    name: 'Nullabc',
    component: Nullabc
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

// vue并不知道上面那个数组就是路由规则，所以把上面的数组传给VueRouter
// VueRouter是vue-router.js暴露出来的一个构造器
const router = new VueRouter({
  base: process.env.BASE_URL,
  routes: routes,
})

export default router
