<template>
  <div class="fabuForm">
    <!-- <el-radio v-model="radio" label="1">在侧边栏添加模块</el-radio> -->
    <!-- <el-radio v-model="radio" label="2">在学院新闻或通知公告添加模块</el-radio> -->
    <div class="whatMoudle" style="margin-top: 20px">
      <el-radio-group v-model="radio">
        <el-radio-button label="在侧边栏添加模块"></el-radio-button>
        <el-radio-button label="在学院新闻或通知公告添加模块"></el-radio-button>
      </el-radio-group>
    </div>
    <br />
    <br />
    <el-form
      :model="ruleForm"
      :rules="rules"
      :label-position="labelPosition"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item class="fabuFormInformName" label="标题" prop="informName">
        <el-input v-model="ruleForm.informName"></el-input>
      </el-form-item>

      <el-form-item
        class="fabuFormInformDate"
        label="发布时间"
        required
        prop="informDate"
      >
        <el-date-picker
          v-model="ruleForm.informDate"
          type="datetime"
          placeholder="选择日期时间"
          default-time="15:00:00"
        ></el-date-picker>
      </el-form-item>

      <!-- <el-form-item label="特殊模块" prop="informUrl">
        <el-radio-group v-model="ruleForm.informUrl">
          <el-radio label="学院新闻"></el-radio>
          <el-radio label="通知公告"></el-radio>
        </el-radio-group>
      </el-form-item> -->

      <el-form-item
        v-if="this.radio == '在学院新闻或通知公告添加模块'"
        class="fabuFormInformUrl"
        label="特殊模块"
        prop="informUrl"
      >
        <el-select v-model="ruleForm.informUrl" clearable placeholder="请选择">
          <el-option label="学院新闻" value="xyxw"> </el-option>
          <el-option label="通知公告" value="tzgg"> </el-option>
        </el-select>
      </el-form-item>

      <el-form-item
        v-if="this.radio == '在侧边栏添加模块'"
        class="fabuFormInformUrl"
        label="发布模块"
        prop="titlesId"
      >
        <!-- 只需为 Cascader 的options属性指定选项数组即可渲染出一个级联选择器。通过props.expandTrigger可以定义展开子级菜单的触发方式。 -->
        <!-- emitPath	在选中节点改变时，是否返回由该节点所在的各级菜单的值所组成的数组，若设置 false，则只返回该节点的值	 -->
        <!-- <el-cascader
          v-model="ruleForm.informUrl"
          :options="options"
          :props="{ expandTrigger: 'hover', emitPath: false }"
        ></el-cascader> -->
        <el-select v-model="ruleForm.titlesId" clearable placeholder="请选择">
          <el-option
            v-for="(item, index) in allTitleName"
            :key="index"
            :label="item.titleName"
            :value="item.titlesId"
          >
          </el-option>
        </el-select>

        <!-- <el-option label="学院新闻" value="xyxw"></el-option>
          <el-option label="人才培养" value="rcpy"></el-option>
          <el-option label="教学工作" value="jxgz"></el-option>
          <el-option label="实验室建设" value="sysjs"></el-option>
          <el-option label="教研科研" value="jyky"></el-option>
          <el-option label="党建工作" value="djgz"></el-option>
          <el-option label="院友之家" value="yyzj"></el-option> -->
        <!-- <el-cascader :props="props"></el-cascader> -->
      </el-form-item>

      <el-form-item class="fabuFormInform" label="发布内容" prop="inform">
        <Editor ref="editor" v-model="ruleForm.inform" />
      </el-form-item>

      <el-form-item class="send">
        <!-- <el-button type="primary" @click="submitUpload">立即创建</el-button> -->
        <el-button type="primary" @click="submitForm('ruleForm')"
          >立即创建</el-button
        >

        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import axios from "axios";
import moment from "moment";
import Editor from "@/components/Editor.vue";
// import qs from "qs";
export default {
  name: "Publish",
  components: {
    Editor,
  },
  // props: [item],
  data() {
    return {
      // 是否特殊模块
      radio: "",
      // props: {
      //   lazy: true,
      //   // 第一个参数node为当前点击的节点，第二个resolve为数据加载完成的回调(必须调用)。
      //   lazyLoad(node, resolve) {
      //     console.log(node);
      //     console.log(resolve);
      //     const { level } = node;
      //     setTimeout(() => {
      //       // 接收导航栏列表
      //       axios
      //         .post("/showTables")
      //         .then((response) => {
      //           console.log(response);
      //           console.log(response.object);
      //           // 接收列表
      //           this.tableListFaBu = response.object;

      //           console.log(Array);
      //           console.log(Array.from);
      //           console.log(Array.from({ length: level + 1 })); // 级联的内容--数组
      //           console.log({ length: level + 1 }); // 每个级联长度多少--数组长度
      //           console.log(level); // 第几个级联--点击的第几个级联

      //           // 为了更准确的显示节点的状态，还可以对节点数据添加是否为叶子节点的标志位 (默认字段为leaf，可通过props.leaf修改)，否则会简单的以有无子节点来判断是否为叶子节点。
      //           const nodes = Array.from({ length: level + 1 }).map((item) => ({
      //             value: item,
      //             label: `${item}`,
      //             // leaf: level >= 2,
      //           }));

      //           console.log(nodes);
      //           console.log(item);
      //           // 通过调用resolve将子节点数据返回，通知组件数据加载完成
      //           resolve(nodes);
      //         })
      //         .catch((error) => {
      //           console.log(error);
      //         });
      //     }, 1000);
      //   },
      // },

      // 表单的label字左对齐
      labelPosition: "left",
      // 是否是修改（编辑）,默认为否，即新发布
      isEditor: false,
      // 发布（编辑）的公告新闻的相关信息
      ruleForm: {
        informName: "", // 标题
        informUrl: null, // 发布模块--tzgg/xyxw
        informDate: "", // 发布时间
        inform: "", // 富文本框内容
        titlesId: null, // 发布模块--侧边栏
      },
      // informUrlFather: "",
      // 表单规则
      rules: {
        // 标题的规则
        informName: [
          {
            required: true, // 必须被填写，有内容
            message: "请输入标题", // 如果没有填写，则显示massage
            trigger: "blur", // 触发方式，blur失去焦点，change数据改变
          },
          // { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        informUrl: [
          {
            // type: "string",
            required: true,
            message: "请选择发布模块",
            trigger: "change", // 数据改变时触发该规则
          },
        ],
        titlesId: [
          {
            // type: "string",
            required: true,
            message: "请选择发布模块",
            trigger: "change", // 数据改变时触发该规则
          },
        ],
        informDate: [
          {
            // type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change",
          },
        ],
        inform: [
          {
            // type: html,
            required: true,
            message: "请填写内容",
            trigger: "change",
          },
        ],
      },
      // tableListFaBu: [],
      // titleListFaBu: [],
      // 模块所需，选项数组
      // options: [],
      allTitleName: [],
    };
  },

  created() {
    /// 判断是编辑还是新发布
    if (this.$store.state.isEdit) {
      console.log(this.$store.state.item);
      // if (this.$store.state.item) {
      // 编辑。在这个页面样式出现之前 判断一下是发布新东西  还是修改旧东西
      // 如果存在点击编辑之后传过来的数据，那么将数据赋值给页面里的变量
      this.ruleForm = this.$store.state.item;
      // }
    }

    /// 接收后台导航栏和侧边栏数据遍历在页面上
    // 接收导航栏列表
    axios
      .post("/showParts")
      .then((response) => {
        console.log(response);
        console.log(response.object);
        // 接收列表
        this.allTitleName = response.object;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    // 提交表单按钮
    submitForm(formName) {
      // 将时间换成 年-月-日 的形式
      this.ruleForm.informDate = moment(this.ruleForm.informDate).format(
        "YYYY-MM-DD HH:mm:ss"
      );
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
          console.log(this.ruleForm); // 打印对象

          console.log("时间：" + this.ruleForm.informDate); //打印时间
          console.log("时间的数据类型：" + typeof this.ruleForm.informDate); // 打印时间类型
          console.log("模块的数据类型：" + typeof this.ruleForm.informUrl); // 打印模块
          console.log("标题的数据类型：" + typeof this.ruleForm.informName); // 打印标题
          console.log("富文本框的数据类型：" + typeof this.ruleForm.inform); // 打印富文本框里的内容
          console.log("时间：" + this.ruleForm.informDate); // 打印时间
          console.log("模块：" + this.ruleForm.informUrl); // 打印模块
          console.log("标题：" + this.ruleForm.informName); // 打印标题
          console.log("富文本框：" + this.ruleForm.inform); // 打印富文本框里的内容
          //
          console.log(this.ruleForm.titlesId);

          if (this.$store.state.isEdit) {
            // 传值给后端--如果是编辑
            axios
              .post("/Alter", this.ruleForm)
              .then((response) => {
                console.log(response);
                if (response.code == 1) {
                  // informUrl\TitlesId全部有值
                  this.$message({
                    type: "info",
                    message: "请只选择一个模块添加文章",
                  });
                } else if (response.code == 0) {
                  // informUrl\TitlesId全部没有值
                  this.$message({
                    type: "info",
                    message: "请至少选择一个模块添加文章",
                  });
                } else {
                  this.$refs[formName].resetFields(); // 重置
                  // 然后把是否是编辑再次改回false
                  this.$store.commit("isEdit", false); // 这里使用 $store.commit 提交 isEdit
                  // 这样显式地提交(commit) mutations,可以让我们更好的跟踪每一个状态的变化，
                  console.log(this.$store.state.isEdit);
                }
              })
              .catch((error) => {
                console.log(error);
              });
          } else {
            // 传值给后端
            axios
              .post("/Fabu", this.ruleForm)
              .then((response) => {
                console.log(response);
                if (response.code == 1) {
                  // informUrl\TitlesId全部有值
                  this.$message({
                    type: "info",
                    message: "请只选择一个模块添加文章",
                  });
                } else if (response.code == 0) {
                  // informUrl\TitlesId全部没有值
                  this.$message({
                    type: "info",
                    message: "请至少选择一个模块添加文章",
                  });
                } else {
                  this.$refs[formName].resetFields(); // 重置
                }
              })
              .catch((error) => {
                console.log(error);
              });
          }

          //
        } else {
          console.log("error submit!!");
          console.log(this.ruleForm.inform);
          console.log(typeof this.ruleForm.inform);

          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    fatherValue(newValue) {
      console.log(newValue);
      // val = val + "";
      this.ruleForm.inform = newValue;
      console.log(this.ruleForm.inform);
    },
    // change(val) {
    //   this.ruleForm.inform = val;
    // },
  },
  beforeDestroy() {
    this.$store.state.item = {};
  },
};
</script>

<style >
.whatMoudle {
  position: absolute;
  top: 30px;
  left: 440px;
}
.fabuForm {
  margin-top: 5%;
  margin-left: 5%;
  width: 90%;
  height: auto;
  /* background: aquamarine; */
}

.fabuFormInformDate {
  width: 50%;
}
.fabuFormInformUrl {
  width: 50%;
}
.fabuFormInformName {
  width: 50%;
}
</style>