import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  // 定义状态
  state: {
    item: {},// 点击编辑，将公告/新闻的那一条对象传值到这里
    isLogin: false,// 是否登陆状态
    isLoginWindows: false,// 是否登陆弹窗页面
    isAdminPage: false,// 是否管理员页面
    isEdit: false,// 是否是编辑状态
    sidebarLeftList: []// 左侧侧边栏的列表，用来初始化的时候接收信息
  },
  mutations: {
    // 其中第一个参数 state 就是 $store.state，第二个参数 msg 需要另外传入
    newItem(state, msg) {
      state.item = msg;
    },
    isLoginWindows(state) {
      state.isLoginWindows = !state.isLoginWindows;
    },
    isLogin(state) {
      state.isLogin = !state.isLogin;
    },
    isAdminPage(state) {
      state.isAdminPage = !state.isAdminPage;
    },
    isEdit(state, msg) {
      // state.isEdit = !state.isEdit;
      state.isEdit = msg;
    },
    newSidebarLeftList(state, msg) {
      state.sidebarLeftList = msg;

    }

  }
})

export default store