<template>
  <div class="homePage">
    <!-- tzgg -->
    <div>
      <div class="noticeTzggTitle noticeTitle">
        <span>
          <!-- <a href="#">...more</a> -->
        </span>
      </div>
      <!-- 公告列表 -->
      <div v-if="!this.$store.state.isLogin" class="noticeContent">
        <ul>
          <li v-for="(item, index) in tzggNoticeList" :key="index">
            <img src="@/assets/native.jpg" />
            <a href="javascript:;" @click="toTzggDetailPage(item)">{{
              item.informName
            }}</a>
            <!-- <router-link to="/detailPage" @click="toDetailPage()">{{item.informName}}</router-link> -->
            <span>{{ item.informDate }}</span>
          </li>
        </ul>
      </div>
      <div v-if="this.$store.state.isLogin" class="noticeContent">
        <ul>
          <li v-for="(item, index) in tzggNoticeList" :key="index">
            <img src="@/assets/native.jpg" />
            <a href="javascript:;" @click="toTzggDetailPage(item)">{{
              item.informName
            }}</a>
            <!-- <router-link to="/detailPage" @click="toDetailPage()">{{item.informName}}</router-link> -->
            <span>{{ item.informDate }}</span>
            <span class="cursorPointer" @click="noticeListDelete(item)"
              >删除</span
            >
            <span class="cursorPointer" @click="noticeListModify(item)"
              >编辑</span
            >
          </li>
        </ul>
      </div>
    </div>

    <!-- xyxw -->
    <div>
      <div class="noticeXyxwTitle noticeTitle">
        <span>
          <!-- <a href="#">...more</a> -->
        </span>
      </div>
      <!-- 公告列表 -->
      <div v-if="!this.$store.state.isLogin" class="noticeContent">
        <ul>
          <li v-for="(item, index) in xyxwNoticeList" :key="index">
            <img src="@/assets/native.jpg" />
            <a href="javascript:;" @click="toXyxwDetailPage(item)">{{
              item.informName
            }}</a>
            <!-- <router-link to="/detailPage" @click="toDetailPage()">{{item.informName}}</router-link> -->
            <span>{{ item.informDate }}</span>
          </li>
        </ul>
      </div>

      <div v-if="this.$store.state.isLogin" class="noticeContent">
        <ul>
          <li v-for="(item, index) in xyxwNoticeList" :key="index">
            <img src="@/assets/native.jpg" />
            <a href="javascript:;" @click="toXyxwDetailPage(item)">{{
              item.informName
            }}</a>
            <!-- <router-link to="/detailPage" @click="toDetailPage()">{{item.informName}}</router-link> -->
            <span>{{ item.informDate }}</span>
            <span class="cursorPointer" @click="noticeListDelete(item)"
              >删除</span
            >
            <span class="cursorPointer" @click="noticeListModify(item)"
              >编辑</span
            >
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
// import NoticeContent from "@/components/NoticeContent";
import axios from "axios";

export default {
  name: "HomePage",
  components: {
    // NoticeContent,
  },

  data() {
    return {
      xyxwNoticeList: [],
      tzggNoticeList: [],
    };
  },
  created() {
    // 页面渲染之前遍历数组，获取公告中数据，展示公告
    // console.log(this.port);
    axios
      .post("/xyxw")
      .then((response) => {
        console.log(response);
        console.log(response.object); // 数组

        this.xyxwNoticeList = response.object;
      })
      .catch((error) => {
        console.log(error);
      });

    axios
      .post("/tzgg")
      .then((response) => {
        console.log(response);
        console.log(response.object); // 数组

        this.tzggNoticeList = response.object;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  // 下面方法函数需要重写
  methods: {
    // 编辑
    noticeListModify(item) {
      console.log(item); //item-(object):inform: "内容3"informDate: "日期3"informName: "tzgg标题3"informUrl: "模块3"

      // 将item传值到发布页面
      this.$store.commit("newItem", item); // 这里使用 $store.commit 提交 newItem,并将 item 传给 msg,从而修改 item
      // 这样显式地提交(commit) mutations,可以让我们更好的跟踪每一个状态的变化，
      console.log(this.$store.state.item); // 这个就是所获取的对象
      console.log("公告编辑");

      // 将编辑切换为ture
      this.$store.commit("isEdit", true); // 这里使用 $store.commit 提交 isEdit
      // 这样显式地提交(commit) mutations,可以让我们更好的跟踪每一个状态的变化，
      console.log(this.$store.state.isEdit);

      this.$router.replace({
        path: "/admin/publishPlus",
        name: "PublishPlus",
      });
    },

    // 删除
    noticeListDelete(item) {
      console.log("index:" + item.index);
      console.log("item:" + item);
      console.log("item.informUrl:" + item.informUrl);
      console.log("item.informName:" + item.informName);
      // this.noticeList.splice(index, 1);
      // alert("删除");
      // 把id传给后台
      axios
        // 删除
        .post(
          "/Delete",
          // {
          // Id: index,
          // informUrl: this.noticeList[index].informUrl
          // }
          item
        )
        .then((response) => {
          console.log(response);
          console.log(response.object); // 数组
          alert("删除成功");

          // 为了刷新页面并且不让页面闪一下影响观感
          this.$router.replace({
            path: "/nullabc.vue",
            name: "Nullabc",
            params: {
              path: "/homePage.vue",
              name: "HomePage",
            },
          });
        })
        .catch((error) => {
          console.log(error);
        });
      console.log("公告删除");
    },

    // 跳转详情页
    toTzggDetailPage(item) {
      // 路由跳转传递参数
      // this.item = this.tzggNoticeList[index];
      // 跳转,这里message为123
      this.$router.push({
        name: `DetailPage`, // 这里只能是name，对应路由
        params: { item: item },
      });
      console.log("点击标题");
      // this.$emit("detailPageRoute", this.item);
    },
    toXyxwDetailPage(item) {
      // 路由跳转传递参数
      // this.item = this.xyxwNoticeList[index];
      // 跳转,这里message为123
      this.$router.push({
        name: `DetailPage`, // 这里只能是name，对应路由
        params: { item: item },
      });
      console.log("点击标题");
      // this.$emit("detailPageRoute", this.item);
    },
  },
};
</script>


<style>
.homePage {
  padding-top: 10px;
}
/* 新闻公告列表 */
.cursorPointer {
  cursor: pointer;
}
.noticeTitle {
  float: left;
  width: 100%;
  height: 30px;
}
.noticeXyxwTitle {
  background: url("../assets/xyxw.png") no-repeat 0 0;
}
.noticeTzggTitle {
  background: url("../assets/tongzhi.jpg") no-repeat 0 0;
}
.noticeTitle span a {
  float: right;
  padding-right: 20px;
  padding-top: 5px;
  font-weight: bolder;
}
.noticeContent {
  float: left;
  width: 100%;
  height: 250px;
  margin-top: 20px;
  background: url("../assets/ctbg.jpg");
  background-size: cover;
  background-position: right;
  background-repeat: no-repeat;
}
.noticeContent ul {
  list-style: none;
  height: 100%;
  overflow: hidden;
}
.noticeContent ul li {
  height: 30px;
  border-bottom: silver 1px dashed;
  line-height: 30px;
}
.noticeContent ul li img {
  float: left;
  margin-right: 10px;
}
.noticeContent ul li a {
  float: left;
  text-decoration: none;
}
.noticeContent ul li span {
  float: right;
  margin-right: 10px;
}
</style>


