<template>
  <div class="addMouduleTab">
    <!-- <HelloWorld msg="Welcome to Your Vue.js App" /> -->
    <div class="addTabModuleButton">
      <el-button style="border-radius: 20px" @click="addModuleTab"
        >添加新导航栏模块</el-button
      >
    </div>

    <!-- 导航栏模块 -->
    <div class="addMouduleTabTableOut">
      <el-table class="addMouduleTabTable" :data="tableData" border>
        <el-table-column prop="tabName" label="导航栏"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button @click="editModuleTab(scope.$index, scope.row)"
              >编辑</el-button
            >
            <el-button
              type="danger"
              @click="deleteModuleTab(scope.$index, scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
import axios from "axios";

export default {
  name: "AddMouduleTab",
  components: {
    // HelloWorld
  },
  data() {
    return {
      tableData: [],
    };
  },
  mounted() {
    // 接收tableData的东西,导航栏列表所有信息，要用对象接收
    // 接收导航栏列表
    axios
      .post("/showTables")
      .then((response) => {
        console.log(response);
        console.log(response.object);
        // 接收列表
        this.tableData = response.object;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    // 添加新的导航栏
    addModuleTab() {
      this.$prompt("请输入新导航栏模块名称", "添加新模块", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputPattern: /\S/,
        inputErrorMessage: "不准为空",
      })
        .then(({ value }) => {
          // 增加模块--增加新的导航栏
          axios
            .post("/CreateTable", {
              tableName: value,
            })
            .then((response) => {
              console.log(response);
              console.log(response.object); // 数组

              if (response.code == 400) {
                this.$message({
                  type: "info",
                  message: value + "导航栏已存在！ ",
                });
              } else {
                // 数据成功回传之后，跳出来massage提示用户所上传的模块的名称
                this.$message({
                  type: "success",
                  message: "新导航栏模块名称: " + value,
                });

                // 为了刷新页面并且不让页面闪一下影响观感
                this.$router.replace({
                  path: "/nullabc.vue",
                  name: "Nullabc",
                  params: {
                    path: "/admin/addModuleTab",
                    name: "AddModuleTab",
                  },
                });
              }
            })
            .catch((error) => {
              console.log(error);

              // 数据回传之后，跳出来massage提示用户error错误
              this.$message({
                type: "info",
                message: error,
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消输入",
          });
        });
    },

    // 编辑以前的导航栏
    editModuleTab(index, row) {
      console.log(index, row);

      // 注释：旧的导航栏名称 row.tabName;

      // 弹框提示输入导航栏新名称
      this.$prompt("请输入此导航栏模块新名称名称", "修改导航栏名称", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputPattern: /\S/,
        inputErrorMessage: "不准为空",
      }).then(({ value }) => {
        // 修改导航栏名称传值 -- axios传值
        axios
          .post("/changeTables", {
            tableName: row.tabName,
            tableNewName: value,
          })
          .then((response) => {
            console.log(response);
            console.log(response.object); // 数组

            // 数据成功回传之后，跳出来massage提示用户所上传的模块的名称
            this.$message({
              type: "success",
              message: "修改后导航栏模块名称: " + value,
            });

            // 为了刷新页面并且不让页面闪一下影响观感
            this.$router.replace({
              path: "/nullabc.vue",
              name: "Nullabc",
              params: {
                path: "/admin/addModuleTab",
                name: "AddModuleTab",
              },
            });
          })
          .catch((error) => {
            console.log(error);

            // 数据成功回传之后，跳出来massage提示用户error错误
            this.$message({
              type: "info",
              message: error,
            });
          });
      });
    },

    // 删除导航栏
    deleteModuleTab(index, row) {
      console.log(index, row);

      axios
        .post("/deleteTable", {
          tableName: row.tabName,
        })
        .then((response) => {
          console.log(response);
          console.log(response.object); // 数组
          // 为了刷新页面并且不让页面闪一下影响观感
          this.$router.replace({
            path: "/nullabc.vue",
            name: "Nullabc",
            params: {
              path: "/admin/addModuleTab",
              name: "AddModuleTab",
            },
          });
        })
        .catch((error) => {
          console.log(error);

          // 数据成功回传之后，跳出来massage提示用户error错误
          this.$message({
            type: "info",
            message: "删除" + row.tabName + "模块报错：" + error,
          });
        });
    },
  },
};
</script>


<style>
.addTabModuleButton {
  float: right;
  padding: 15px;
  margin: 0 auto;
}
.addMouduleTabTableOut {
  margin: 10px;
}
.addMouduleTabTable {
  width: 50%;
  border-radius: 10px;
}
</style>