<template>
  <div class="admin">
    <!-- <HelloWorld msg="Welcome to Your Vue.js App" /> -->
    <div class="h1">
      <el-button class="exit" @click="PageOut()">返回页面</el-button>
      <h1>后台管理界面</h1>
    </div>

    <div class="adminMain">
      <!-- 左侧菜单栏 -->
      <div class="adminLeft">
        <el-menu
          default-active="/admin/addModule"
          router
          class="el-menu-vertical-demo adminSideMenuList"
          @open="handleOpen"
          @close="handleClose"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
        >
          <el-menu-item index="/admin/addModule">
            <i class="el-icon-plus"></i>
            <span>添加模块</span>
          </el-menu-item>
          <el-menu-item index="/admin/publishPlus">
            <i class="el-icon-set-up"></i>
            <span>发布文章</span>
          </el-menu-item>
          <el-menu-item index="/admin/fileUpload">
            <i class="el-icon-upload"></i>
            <span>文件上传</span>
          </el-menu-item>
          <el-menu-item @click="logOut()">
            <i class="el-icon-upload"></i>
            <span>退出登录</span>
          </el-menu-item>
        </el-menu>
      </div>
      <!-- 右侧展示栏 -->
      <div class="adminRightOut">
        <div class="adminRight">
          <router-view />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
// import PublishPlus from "@/components/PublishPlus.vue";
// import FileUpload from "@/components/FileUpload.vue";

export default {
  name: "Admin",
  components: {
    // HelloWorld
    // PublishPlus,
    // FileUpload,
  },
  data() {
    return {
      tabPosition: "left", // 左侧导航左对齐
    };
  },
  methods: {
    // 左侧导航打开和关闭
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    // 退出登录
    logOut() {
      // 登录状态切换为false
      this.$store.commit("isLogin"); // 这里使用 $store.commit 提交 isLogin（函数）
      // 这样显式地提交(commit) mutations,可以让我们更好的跟踪每一个状态的变化，
      console.log(this.$store.state.isLogin);

      // 返回首页面
      this.$router.push("/");
    },
    PageOut() {
      // 返回首页面
      this.$router.push("/");
    },
  },
  mounted() {},
};
</script>


